<template>
  <el-table
    :data="tableData.filter(data => !search || data.no.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">
    <el-table-column
      label="唯一标识符"
      prop="serial">
    </el-table-column>
    <el-table-column
      label="状态"
      prop="state">
    </el-table-column>
    <el-table-column
      label="型号"
      prop="model">
    </el-table-column>
    <el-table-column
      label="设备"
      prop="device">
    </el-table-column>
    <el-table-column
      label="平台"
      prop="product">
    </el-table-column>
    <el-table-column
      align="right">
      <template slot="header">
        <el-input
          v-model="search"
          size="mini"
          placeholder="输入关键字搜索"/>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleShell(scope.$index, scope.row)">Shell</el-button>
        <el-button
          size="mini" type="primary"
          @click="handleCtrl(scope.$index, scope.row)">控制</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">断开</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import { lists } from '../api/devices.js'
  
  export default {
    data() {
      return {
        tableData: [],
        search: ''
      }
    },
    mounted(){
      this.loadList()
    },
    methods: {
      loadList() {
        lists().then( (res) => {
          this.tableData = res.data
        })
      },
      handleShell(index, row) {
        console.log(index, row);
        this.$router.push({ path: 'terminal', query: { serial: row.serial}})
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleCtrl(index, row) {
        console.log(index, row);
        this.$router.push({ path: 'control', query: { serial: row.serial}})
      }
    },
  }
</script>