<template>
  <img v-bind:src="imageSrc" :style="'width: '+ width +';height: ' + height + ';' "/>
</template>

<script>
import { windowSize } from '../api/devices.js'

export default {
  name: 'Screencap',
  data() {
    return {
      imageSrc: 'http://127.0.0.1:8081/api/device/screencap',
      width: "200px",
      height: "400px"
    }
  },
  created: function () {
    setInterval(this.timer, 10000);
  },
  methods: {
    timer: function () {
        this.imageSrc = "http://127.0.0.1:8081/api/device/screencap?channel=shell&serial=" + this.serial + "&time=" + new Date().getTime();
    }
  },
  props: {
    serial: String
  },
  mounted() {
    windowSize({serial: this.serial}).then((res) => {
        var x = res.data.height  * 200;
        var h = x / res.data.width ;
        this.width = "200px"
        this.height = h + "px"
    });
    this.imageSrc = "http://127.0.0.1:8081/api/device/screencap?channel=shell&serial=" + this.serial;
  }
}
</script>